import moment from 'moment'
import { helpers } from 'vuelidate/lib/validators'


export const validarCpf = helpers.withParams(
    {type: 'cpfValidator'},
    (cpf) => validarCPF(cpf));

export const validarCnpj = helpers.withParams(
    {type: 'cnpjValidator'},
    (cnpj) => validarCNPJ(cnpj));


export const validarCpfCnpj = (param, estrangeiro) => helpers.withParams(
    {type: 'cpfCnpjValidator' },
    (data) => {
        console.log(param, estrangeiro);
        if(!data || estrangeiro)
            return true;
        switch(param){
            case 'FISICA': return validarCPF(data);
            case 'JURIDICA': return validarCnpj(data);
            default: return false;
        }
        
    });

export const notInFuture = helpers.withParams( 
    
    {type: 'InvalidDate'} ,
    (date_field) =>   {
        if(!date_field || date_field.length == 0)
            return true;
        if(date_field.length < 8)
            return false;
        if(date_field.length == 8)
            return moment(date_field,  "DD/MM/YYYY").isSameOrBefore() 
        else
            return moment(date_field,  "DD/MM/YYYY").isSameOrBefore()  
        
    })



const validarCPF = (cpf) => {  
    if(!cpf || cpf.length == 0)
        return true

    if( cpf.length < 11)
        return false;

    let soma = 0;
    let resto;
    cpf = cpf.replace(/[^\d]+/g,''); // Somente os numeros
    if( cpf.length != 11 || cpf == '00000000000')
        return false;

    for (let i=1; i<=9; i++) soma = soma + parseInt(cpf.substring(i-1, i)) * (11 - i);
    resto = (soma * 10) % 11;
    
        if ((resto == 10) || (resto == 11))  resto = 0;
        if (resto != parseInt(cpf.substring(9, 10)) ) return false;
    
    soma = 0;
        for (let i = 1; i <= 10; i++) soma = soma + parseInt(cpf.substring(i-1, i)) * (12 - i);
        resto = (soma * 10) % 11;
    
        if ((resto == 10) || (resto == 11))  resto = 0;
        if (resto != parseInt(cpf.substring(10, 11) ) ) return false;
        return true;

}



const validarCNPJ = (cnpj) => {
 
    if(!cnpj || cnpj.length == 0)
        return true;
    if(cnpj.length < 11)
        return false;
    cnpj = cnpj.replace(/[^\d]+/g,'');
 
    if(cnpj == '') return false;
     
    if (cnpj.length != 14)
        return false;
 
    // Elimina CNPJs invalidos conhecidos
    if (cnpj == "00000000000000" || 
        cnpj == "11111111111111" || 
        cnpj == "22222222222222" || 
        cnpj == "33333333333333" || 
        cnpj == "44444444444444" || 
        cnpj == "55555555555555" || 
        cnpj == "66666666666666" || 
        cnpj == "77777777777777" || 
        cnpj == "88888888888888" || 
        cnpj == "99999999999999")
        return false;
         
    // Valida DVs
    let tamanho = cnpj.length - 2
    let numeros = cnpj.substring(0,tamanho);
    let digitos = cnpj.substring(tamanho);
    let soma = 0;
    let     pos = tamanho - 7;
    for (let i = tamanho; i >= 1; i--) {
      soma += numeros.charAt(tamanho - i) * pos--;
      if (pos < 2)
            pos = 9;
    }
    let resultado = soma % 11 < 2 ? 0 : 11 - soma % 11;
    if (resultado != digitos.charAt(0))
        return false;
         
    tamanho = tamanho + 1;
    numeros = cnpj.substring(0,tamanho);
    soma = 0;
    pos = tamanho - 7;
    for (let i = tamanho; i >= 1; i--) {
      soma += numeros.charAt(tamanho - i) * pos--;
      if (pos < 2)
            pos = 9;
    }
    resultado = soma % 11 < 2 ? 0 : 11 - soma % 11;
    if (resultado != digitos.charAt(1))
          return false;
           
    return true;
}

export const extenso = function(vlr){

    let Num=parseFloat(vlr);
    if (vlr == 0){
        document.getElementById('teste').value = "zero";
    }else{

        let inteiro = parseInt(vlr);; // parte inteira do valor
        if(inteiro<1000000000000000){

        let resto = Num.toFixed(2) - inteiro.toFixed(2);       // parte fracionária do valor
        resto=resto.toFixed(2)
        let vlrS =  inteiro.toString();    

        let cont=vlrS.length;
        let extenso="";
        let auxnumero;
        let auxnumero2;
        let auxnumero3;

        let unidade =["", "um", "dois", "três", "quatro", "cinco",
        "seis", "sete", "oito", "nove", "dez", "onze",
        "doze", "treze", "quatorze", "quinze", "dezesseis",
        "dezessete", "dezoito", "dezenove"];

        let centena = ["", "cento", "duzentos", "trezentos",
            "quatrocentos", "quinhentos", "seiscentos",
            "setecentos", "oitocentos", "novecentos"];

        let dezena = ["", "", "vinte", "trinta", "quarenta", "cinquenta",
            "sessenta", "setenta", "oitenta", "noventa"];

        let qualificaS = ["reais", "mil", "milhão", "bilhão", "trilhão"];
        let qualificaP = ["reais", "mil", "milhões", "bilhões", "trilhões"];

        for (let i=cont;i > 0;i--){   
            let verifica1="";
            let verifica2=0;
            let verifica3=0;
            auxnumero2="";
            auxnumero3="";
            auxnumero=0;
            auxnumero2 = vlrS.substr(cont-i,1);    
            auxnumero = parseInt(auxnumero2);


            if((i==14)||(i==11)||(i==8)||(i==5)||(i==2)){
                auxnumero2 = vlrS.substr(cont-i,2);
                auxnumero = parseInt(auxnumero2);
            }

            if((i==15)||(i==12)||(i==9)||(i==6)||(i==3)){
                extenso=extenso+centena[auxnumero];
                auxnumero2 = vlrS.substr(cont-i+1,1)
                auxnumero3 = vlrS.substr(cont-i+2,1)

                if((auxnumero2!="0")||(auxnumero3!="0"))
                extenso+=" e ";

            }else if(auxnumero>19){
                auxnumero2 = vlrS.substr(cont-i,1);
                auxnumero = parseInt(auxnumero2);
                extenso=extenso+dezena[auxnumero];
                auxnumero3 = vlrS.substr(cont-i+1,1)

                if((auxnumero3!="0")&&(auxnumero2!="1"))
                extenso+=" e ";
            }else if((auxnumero<=19)&&(auxnumero>9)&&((i==14)||(i==11)||(i==8)||(i==5)||(i==2))){
                    extenso=extenso+unidade[auxnumero];   
            }else if((auxnumero<10)&&((i==13)||(i==10)||(i==7)||(i==4)||(i==1))){
                auxnumero3 = vlrS.substr(cont-i-1,1);
                if((auxnumero3!="1")&&(auxnumero3!=""))
                extenso=extenso+unidade[auxnumero]; 
            }

            if(i%3==1){
                verifica3 = cont-i;
                if(verifica3==0)
                    verifica1 = vlrS.substr(cont-i,1);

                if(verifica3==1)
                    verifica1 = vlrS.substr(cont-i-1,2);

                if(verifica3>1)
                    verifica1 = vlrS.substr(cont-i-2,3);

                verifica2 = parseInt(verifica1);

                if(i==13){
                    if(verifica2==1){
                    extenso=extenso+" "+qualificaS[4]+" ";
                    }else if(verifica2!=0){extenso=extenso+" "+qualificaP[4]+" ";}
                }
                if(i==10){
                    if(verifica2==1){
                        extenso=extenso+" "+qualificaS[3]+" ";
                    }else if(verifica2!=0){extenso=extenso+" "+qualificaP[3]+" ";}
                }
                if(i==7){
                        if(verifica2==1){
                            extenso=extenso+" "+qualificaS[2]+" ";
                    }else if(verifica2!=0){extenso=extenso+" "+qualificaP[2]+" ";}
                }
                if(i==4){
                    if(verifica2==1){
                       extenso=extenso+" "+qualificaS[1]+" ";
                    }else if(verifica2!=0){extenso=extenso+" "+qualificaP[1]+" ";}
                }
                if(i==1){
                    if(verifica2==1){
                        extenso=extenso+" "+qualificaS[0]+" ";
                }else {extenso=extenso+" "+qualificaP[0]+" ";}
                }
            }
        }
        resto = resto * 100;
        let aexCent=0;
        if(resto<=19&&resto>0)
        extenso+=" e "+unidade[resto]+" Centavos";
        if(resto>19){
            aexCent=parseInt(resto/10);

            extenso+=" e "+dezena[aexCent]
            resto=resto-(aexCent*10);

            if(resto!=0)
            extenso+=" e "+unidade[resto]+" Centavos";
            else extenso+=" Centavos";
        }

        return extenso;
    }
    else
        return "Numero maior que 999 trilhões"
    }
}