import container from '../../../components/forms/container.vue';

const userService = new UserService();
const enderecoService = new EnderecoService();

export default {
  components: { container },

  data() {
        return {
            form: {
                id: null,
                nome: '',
                sobrenome: '',
                cpf: '',
                rg: '',
                rg_estado: '',
                email_verified_at: '',
                password: '',
                confirm_password: '',
                remember_token: '',
                cidade_id: '',
                endereco_tipo: '',
                logradouro: '',
                numero: '',
                complemento: '',
                bairro: '',
                cep: '',
                fone: '',
                sexo: '',
                data_nascimento: '',
                email: '',
                dono_assinatura: false,
                ativo: true,
            },
            formSenha: {
                newPassword: '',
                confirmPassword: '',
            },
            sucesso: null,
            erros:null,
            errosTitulo:'',
            errosRoles: null,
            sucessoRoles: null,

            roles: [],
            roleModal: null,
            rolesBusca: [],
            timer: null,
            buscandoRole: true,
            cidades: [],
            cidade:{},
            errosSenha: null,
            sucessoSenha: null
        } ;
    },
    validations: {
        form: {
            nome: { required, maxLength: maxLength(200) },
            sobrenome: { required, maxLength: maxLength(200) },
            cpf: {  maxLength: maxLength(20), validarCpf:validarCpf },
            rg: { maxLength: maxLength(20) },
            rg_estado: {},
            cidade_id: {},
            endereco_tipo: {},
            logradouro: { maxLength: maxLength(100) },
            numero: { maxLength: maxLength(10) },
            complemento: { maxLength: maxLength(100) },
            bairro: { maxLength: maxLength(100) },
            cep: { maxLength: maxLength(10) },
            fone: { required, maxLength: maxLength(20) },
            sexo: { },
            data_nascimento: { notInFuture: notInFuture },
            email: { required, maxLength: maxLength(150), email },
            dono_assinatura: { required },
            ativo: { required },

            password: { 
                required: requiredIf(function () {
                    return !this.form.id;
                }),
                sameAs: sameAs("confirm_password")
            },
            confirm_password: { 
                sameAs: sameAs("password")
            }
        },
        formSenha: {
                newPassword: { required, minLength: minLength(6),  maxLength: maxLength(20) },
                confirmPassword: { required, sameAs: sameAs("newPassword") },
            },
    },
    mounted() {
        if (this.$route.params.id) {
            this.buscarObjeto(this.$route.params.id);
        }       
    },
    methods: {

        submit: function(){

            this.$v.form.$touch();
            this.erros = null;
            this.sucesso = null;    
            this.errosTitulo ='';

            if (this.$v.form.$invalid){
                this.errosTitulo = 'Preencha o formulário corretamente, alguns campos estão com erros!'
                this.erros = this.$v.form;
                return false;
            }
            let obj = {...this.form};
            if(obj.data_nascimento){
                obj.data_nascimento = moment(obj.data_nascimento,  "DD/MM/YYYY").format('YYYY-MM-DD')
            }
            let request = null;
            if(obj.id)    
                request = userService.atualizar(obj);
            else
                request = userService.criar(obj);

            request.then(
                response => {
                    let redireciona = !this.form.id;
                    this.form.id = response.data.id;
                    
                    this.sucesso = "Registro salvo com sucesso!";
                    if(redireciona) {
                        let index = String(this.$route.path).lastIndexOf("/");
                        let newPath = String(this.$route.path).slice(0, index);
                        newPath += "/" + this.form.id;
                        this.$router.push(newPath);
                        this.buscarObjeto(this.form.id);
                    }
                }   
            ).catch( erro => this.erros = erro )

        },

        buscarObjeto: function(id){
            userService.buscar(id)
                .then(  response => {
                    this.form = response.data;
                    this.roles = response.data.roles;
                    this.form.data_nascimento = this.form.data_nascimento ? moment(this.form.data_nascimento).format("DD/MM/YYYY") : "";
                    this.cidade = response.data.cidade ? { id: response.data.cidade.id, nome: response.data.cidade.nome, sigla: response.data.cidade.estado.sigla } : {};
                }).catch( erro => this.erros = erro)
        },

        buscarCep: function(){
            enderecoService.buscarEnderecoPorCep(this.form.cep)
                .then((response) => {
                console.log(response);
                if(response.data.logradouro){
                    this.form.logradouro = response.data.logradouro.substr( response.data.logradouro.indexOf(' ')+1 );
                    this.form.endereco_tipo = response.data.logradouro.substr(0, response.data.logradouro.indexOf(' '));
                }else{
                    this.form.logradouro = '';
                    this.form.endereco_tipo = '';
                }
                this.form.bairro = response.data.bairro;
                enderecoService.buscarCidadePorIbge(response.data.ibge)
                    .then((responseCidade) => {
                        console.log(responseCidade.data);
                        this.cidade = { id: responseCidade.data.id, nome:responseCidade.data.nome ,sigla: responseCidade.data.estado.sigla };
                        this.form.cidade_id = this.cidade.id;
                        this.cidades = [];
                    })
                    .catch((errorCity) => {
                        console.log(errorCity);
                    });
                })
                .catch((error) => {});
        },

        buscarCidadesDebounce: function(text, loading){
            clearTimeout(this.timer);
            this.timer = setTimeout(() => {
                this.buscarCidades(text, loading)
            }, 700);
        },

        buscarCidades: function(text, loading){
            enderecoService.listarCidadesBusca({texto: text})
                .then( response =>  this.cidades = response.data.data )
                .catch( error => console.log(error))
        },


        abrirModalTrocarSenha: function(){
            this.$v.formSenha.$reset();
            this.formSenha.newPassword = '',
            this.formSenha.confirmPassword = '';
            this.errosSenha = null;
            this.sucessoSenha = null;

        },

        trocarSenha: function(){
            this.errosSenha = null;
            this.sucessoSenha = null;
            this.$v.formSenha.$touch();
            if(this.$v.formSenha.$invalid)
                return false;

            userService.changePass({...this.formSenha, id: this.form.id})
                .then(response => {
                    this.abrirModalTrocarSenha();
                    this.sucessoSenha = 'Senha Alterada com sucesso!';
                }).catch(erro => this.errosSenha = erro);
        },
        
        abrirModalDesvincular: function(role){
            this.sucessoRoles = null;
            this.errosRoles = null;
            this.roleModal = {...role};
        },

        desvincularRole: function(){
            this.sucessoRoles = null;
            this.errosRoles = null;
            userService.desvincularGrupo(this.form.id, this.roleModal.id)
            .then( response => {
                this.roles = this.roles.filter(x => x.id != this.roleModal.id);
                this.sucessoRoles = 'Grupo '+this.roleModal.name+' desvinculado com sucesso!';
            }).catch( erro => this.errosRoles = erro)
        },

         abrirModalVincular: function(){
            this.sucessoRoles = null;
            this.errosRoles = null;
            this.roleModal = null;
            this.buscarRoles();
        },

        vincularRole: function(){
            this.sucessoRoles = null;
            this.errosRoles = null;
            userService.vincularGrupo(this.form.id, this.roleModal.id)
            .then( response => {
                this.roles.push({...response.data}) ;
                this.sucessoRoles = `Grupo: ${response.data.name} vinculado com sucesso!`;
            }).catch( erro => this.errosRoles = erro)
        },

        buscarRolesDebounce: function(text, loading){
            clearTimeout(this.timer);
            this.timer = setTimeout(() => {
                this.buscarRoles(text, loading)
            }, 700);
        },

        buscarRoles: function(text, loading){
            this.rolesBusca = [];
            this.buscandoRole = true;
            userService.listarGrupos({texto: text, per_page:50, user_id: this.form.id})
                .then( response => this.rolesBusca = response.data.data)
                .catch( erro => this.errosRoles = erro)
                .finally( () => this.buscandoRole = false)
        }

    }
};
</script>
